<template>
    <div>
        <top-header :active="'6'"></top-header>
        <div style="height:90px"></div>
        <!-- <button @click="Fnknowledgelistv3">获取列表内容</button> -->
        <div class="NConter">
          <div class="ConLeft">
            <!-- 这是新闻轮播图 -->
            <div class="ConBanner">
              <el-carousel height="450px">
                <el-carousel-item v-for="(Bitem,Bindex) in bannerList" :key="Bindex">
                  <!-- <h3 class="small">{{ item }}</h3> -->
                  <h3>{{ Bitem.tit }}</h3>
                  <img :src="Bitem.imgSrc" alt="">
                </el-carousel-item>
              </el-carousel>
            </div>
            <!-- 导航主题 -->
            <ul class="Nav">
              <li v-for="(Titem,Tindex) in tagList" :key="Tindex" @click="FnNav(Titem)" :class="{liActive:NavActive==Titem}">{{Titem}}</li>
            </ul>
            <!-- 这是新闻条目 -->
            <div class="newsList" v-for="(item,index) in listNT" :key="index" @click="gotodetails(item.k_id)">
              <h4>{{item.k_title}}</h4>
              <p>{{item.k_content | rephtml}}</p>
              <div>作者：{{item.k_user_name}} 时间：{{item.k_date | toTime}}</div>
            </div>
            <!-- 分页 -->
            <el-pagination
            background
            layout="prev, pager, next"
            :current-page="currentPage"
            @current-change="handleCurrentChange"
            :page-size="PageSize"
            :total="total">
            </el-pagination>
          </div>
          <!-- 这边是左边的的侧边栏，珠岙存放日历和热点新闻 -->
          <div class="dateBox">
            <!-- 这是日历，后续可以研究是否在细节优化一下 -->
            <el-calendar v-model="value">
            </el-calendar>
            <!-- 这里是热点新闻 -->
            <div class="hotspot">
              <h3>最新资讯</h3>
              <div v-for="(itemL,indexL) in LatestNews" :key="indexL" @click="gotodetails(itemL.k_id)">
                {{itemL.k_title}}
                <span>时间：{{itemL.k_date | toTime}}</span>
              </div>
            </div>
          </div>
        </div>

        <div class="footer">
            © 黄岛达摩智联科技有限公司版权所有 浙ICP备15005406号-1
        </div>
    </div>
</template>
<script>
import topHeader from "./Header";
import url from '../../common/api'
import { mapState } from "vuex";
export default {
  data() {
    return {
      value: new Date(),
      bannerList:[//banner
          {
              imgSrc:require('../../assets/img/banner1.jpg'),
              tit:'高性能高可靠',
          },
          {
              imgSrc:require('../../assets/img/banner2.jpg'),
              tit:'规则引擎'
          },
          {
              imgSrc:require('../../assets/img/banner3.jpg'),
              tit:'数据实时分析'
          },
      ],
      NavList:[
        '汽轮机',
        '垃圾处理',
        '天然气',
        '压力容器',
      ],
      NavActive:'',
      listN:[],
      listNT:[],
      total:0,//分页-总条目
      currentPage:1,//分页-当前页数
      PageSize:7,//每页有多少条数
      LatestNews:''//最新资讯
    }
  },
  mounted(){
    this.FnNav()//开始显示的数据
    this.Fnknowledgelistv4()
  },
  methods:{
    FnNav(item){//这是导航栏选中的是什么主题的
      this.NavActive=item || '汽轮机'
      url.knowledgelistv3({
        k_tag:this.NavActive,
        page:this.currentPage,
        per_page:this.PageSize,//每页显示条数
      }).then(res=>{
        console.log(res,'-----------数据的开始数据')
        if(res.data.code[0]='200'){
            let xx=res.data.data
            let newlist=[]
            xx.forEach(element => {
                element.fields.k_id=element.pk
                newlist.push(element.fields)
            });
            this.listNT=newlist
            this.total=res.data.datacount
        }
      })
    },
    handleCurrentChange(val) {//点击分页功能实现数据切换
      this.currentPage=val
      this.knowledgelistv2()
    },
    knowledgelistv2(){//行业资讯列表
      console.log('----------点击了按钮')
      url.knowledgelistv2({
        page:this.currentPage,
        per_page:this.PageSize,//每页显示条数
      })
      .then(res=>{
        if(res.data.code[0]='200'){
            let xx=res.data.data
            let newlist=[]
            xx.forEach(element => {
                element.fields.k_id=element.pk
                newlist.push(element.fields)
            });
            this.listNT=newlist
            this.total=res.data.datacount
        }
      })
    },
    Fnknowledgelistv4(){
      url.knowledgelistv4()
      .then(res=>{
        console.log(res,'---LatestNews-------五条数据')
        if(res.data.code[0]='200'){
            let xx=res.data.data
            let newlist=[]
            xx.forEach(element => {
                element.fields.k_id=element.pk
                newlist.push(element.fields)
            });
            this.LatestNews=newlist
        }
      })
    },
    gotodetails(k_id){
      this.$router.push({
        path:'/newsDetH', //也可以在路径上拼接
        query:{
          k_id:k_id,       //传递的值
          k_type:'行业资讯'
        }
      })
    },
    xd(){
      console.log('点击的小可爱=--------')
    }
  },
  components: {
    topHeader
  },
  computed:{
    ...mapState(['tagList'])
  }
}
</script>
<style lang='stylus' scoped>
    .top 
      height: 92px;
    .footer
      background: rgb(1, 21, 54);
      line-height: 60px;
      color: #ffffff;
      text-align: center;
      margin-top: 50px;
    .NConter
      width 1200px
      margin 0 auto
      display flex
      .ConLeft
        flex 1
        .ConBanner
          width 770px
          margin-top 30px
          position relative
          img 
            width 100%
            height 100%
          h3 
            position absolute
            top 0
            left 0
            height 50px
            background rgba(000,000,000,0.3)
            color #fff
            width 100%
            line-height 50px
            padding-left 25px
            font-size 15px
        .Nav 
          border-top: 3px solid #245399;
          border-bottom: 1px solid #cccccc;
          height: 70px;
          font-size: 24px;
          color: #666;
          width 770px
          li 
            float left
            padding 0 18px
            border-right 1px solid #666
            height 26px
            margin-top 25px
          li:last-of-type
            border-right none
          .liActive
            color #c31f0b
        .newsList
          width: 770px;
          height: auto;
          padding-top: 5px;
          padding-bottom: 20px;
          border-bottom: 1px solid #cccccc;
          h4 
            color: #333333;
            font-weight: bold;
            font-size: 19px;
            line-height: 43px;
            font-weight: bold;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap
          p 
            font-size: 15px;
            line-height: 25px;
            padding-top: 3px;
            color: #666;
            text-indent: 0px;
            text-align: justify;
            *height: 77px;
            height: auto;
            overflow:hidden; 
            text-overflow:ellipsis;
            display:-webkit-box; 
            -webkit-box-orient:vertical;
            -webkit-line-clamp:2; 
          div 
            line-height: 15px;
            font-size: 15px;
            margin-top: 0;
            color: #999999;
            display: inline-block;
        .pageC
          margin-top 25px
      .dateBox
        width 400px
        .hotspot
          width 360px
          margin-left 20px
          box-sizing border-box
          padding 20px
          background #f1f1f1
          h3 
            font-size: 16px;
            padding-left: 12px;
            border-left: 4px solid #ec7259;
            margin-bottom 15px
          div 
            line-height 24px
            font-size 16px
            color #333
            border-bottom 1px solid #ccc
            z-index 999999
            span 
              color #ccc
              font-size 14px
              display block
              line-height 18px
</style>


